<template>
    <div id="app">
        <keep-alive>
        <!-- 需要缓存的视图组件 --> 
            <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <!-- 不需要缓存的视图组件 -->
        <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
</template>
<script>
import wx from 'weixin-js-sdk'
export default {
    data(){
        return {
        }
    },
    watch: {
        '$route' () {
            this.getCheckTitle()
        }
    },
    mounted(){
        this.getCheckTitle();
    },
    methods:{
        //判断从哪个页分享的
        getCheckTitle(){
            var re = /invite/;
            let is_url = re.test(document.URL)
            if(is_url == true){
                this.share2();
            }else{
                this.share();
            }
        },
        //分享本页
        share(){
            let ua = window.navigator.userAgent.toLowerCase();
            if(ua.indexOf('micromessenger') > 1){
                var that=this;
                this.$http.get('/main/shareConfig').then(function (res) {
                    that.wxshare(res.data,'天工帮 | 务工服务平台','“天下务工，帮您成功”各地就业部门共同指导的优质务工服务平台｜求职招聘｜灵活用工｜生活服务','https://rlzy.teambook.cc/#/logo','https://quickrequest.oss-cn-beijing.aliyuncs.com/image/189cb80be4deffe50950b9226132769.png')
                }).catch(function () {
                });
            }
        },
        //分享invite页
        share2(){
            let ua = window.navigator.userAgent.toLowerCase();
            if(ua.indexOf('micromessenger') > 1){
                var that=this;
                this.$http.get('/main/shareConfig').then(function (res) {
                    that.wxshare(res.data,'天工帮 | 务工服务平台','“天下务工，帮您成功”各地就业部门共同指导的优质务工服务平台｜求职招聘｜灵活用工｜生活服务','https://rlzy.teambook.cc/#/logo','https://quickrequest.oss-cn-beijing.aliyuncs.com/image/189cb80be4deffe50950b9226132769.png')
                }).catch(function () {
                });
            }
        },
        wxshare(res,title,desc,links,imgUrl){
            wx.config({
                debug: false,
                appId: res.appId,
                timestamp: res.timestamp,
                nonceStr: res.noncestr,
                signature: res.signature,
                jsApiList: [
                    'updateAppMessageShareData','updateTimelineShareData'
                ]
            });
            wx.ready(function () {
                wx.updateAppMessageShareData({
                    title: title, // 分享标题
                    desc: desc, // 分享描述
                    link:links, // 分享链接
                    imgUrl: imgUrl, // 分享图标
                    success: function () {
                    }
                });
                //微信分享菜单测试
                wx.updateTimelineShareData({
                    title:title, // 分享标题
                    link: links, // 分享链接
                    imgUrl: imgUrl, // 分享图标
                    success: function () {
                    }
                });
            });
            wx.error(function(err){
                console.log(err)
            });
        }
    }
}
</script>
