<template>
<transition name="slide-fade">
	<div class="cc-main">
		<img class="cc-logo" src="../assets/img/logo.png" alt="">
	</div>
</transition>
</template>
<script>
import {Toast} from 'vant';
export default {
	name: 'logo',
	components: {
		[Toast.name]: Toast,
	},
	data() {
		return {
			again:0,
		};
	},
	created() {
		if(process.env.NODE_ENV !== 'production'){
			localStorage.setItem("tgbtoken1",'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJvcGVuSWQiOiJvTXpmanc4RU1XTmx2ekhaQVlKbUpwNjN4dE5RIiwiaXNzIjoidGVhbWJvb2tfdGdiIiwibW9iaWxlIjoiMTUyNTcxNjY1MDYiLCJ1dWlkIjoiNDJhMjI3MzNiYjAxYzJkMjZjMDVkYzFkY2UzN2ZmMWYiLCJ1c2VySWQiOiIxMTgifQ.FYW5cojSqoH2TDmQetzIWCyEqSPyDmJhuy_H8hNRFvE');
			this.$router.push({
				path:"/role"
			})
		}else{
			this.login3();
		}
	},
	methods: {
		login3(){
			let ua = window.navigator.userAgent.toLowerCase();//是否是微信打开
			if (ua.match(/MicroMessenger/i) == "micromessenger") {
				let check_code = document.URL.match(/code=(\S*)&state/);
				if (check_code == null) {
					window.location.replace("https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx325742af857de04b&redirect_uri=" +encodeURIComponent(document.URL) +"&response_type=code&scope=snsapi_base&state=1#wechat_redirect");
				} else {
					this.login();
				}
			}else{  
				Toast.fail("请在微信里打开");
			}
		},
		login() {
			var code = '';
			var codeStr = document.URL.match(/code=(\S*)&state/);
			if(codeStr){
				code = document.URL.match(/code=(\S*)&state/)[1];
			}
			var that=this;
			this.$http.post('/main/wxLogin?code='+code).then(function (res) {
				if (res.code==10000) {
					localStorage.setItem('tgbtoken1',res.data.token);
					localStorage.setItem('tgbissign1',res.data.isSign);
					localStorage.setItem('tgbiscompany1',res.data.isCompany);
					localStorage.setItem('tgbisintermediary1',res.data.isIntermediary);
					var url='https://rlzy.teambook.cc/#';
					var url1="";
					var role=that.$route.query.role;
					if (res.data.isMobile) {
						if (role==1) {
							url1=url+'/home';
						}else if(role==2){
							if (res.data.isIntermediary==1) {
								url1=url+'/home01';
							}else{
								url1=url+'/home1';
							}
						}else if(role==3){
							if (res.data.isCompany==1) {
								url1=url+'/home2';
							}else{
								url1=url+'/checkin';
							}
						}else{
							url1=url+'/role';
						}
					}else{
						if (role>0) {
							url1=url+'/login?role='+role; 
						}else{
							url1=url+'/login'; 
						}
					}
					window.location.replace(url1);
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	}
};
</script>
<style lang="scss" scoped>
.cc-main{
	width: 375px;
	height:100vh;
	background-image: radial-gradient(#3396d4, #073275);
	padding-top: 278px;
	img{
		width: 63px;
		height: 64px;
		margin:0 auto;
	}
}
</style>