import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import 'amfe-flexible'
import '@/assets/css/style.css'//基础样式
import { Toast } from 'vant';
Vue.config.productionTip = false
Vue.prototype.$http=axios;
// 基础url
if(process.env.NODE_ENV !== 'production'){
	axios.defaults.baseURL = '/';
	// localStorage.setItem("tgbtoken1",'123456');
}else{
	axios.defaults.baseURL = 'https://app.teambook.cc/rlzy2020';
	// axios.defaults.baseURL = 'https://app.teambook.cc/rlzy_test';
}
// 跳转页面前 修改标题并判断下一页面是否需要token
router.beforeEach((to, from, next) => {
	if (to.meta.title) {
		document.title = to.meta.title;
	}else{
		document.title = "天工帮务工服务平台";
	}
	let islogin = localStorage.getItem("tgbtoken1");
	// localStorage.setItem('beforeLoginUrl', to.fullPath)
	if(to.path == "/logo"){
		next();
	}else{
		if(to.meta.requireAuth) {
			if (islogin) {
				next();
			}else{
				next("/logo");
			}
		}else{
			next();
		}
	}
})
// 请求前自动添加token
axios.interceptors.request.use(
	config => {
		let AUTH_TOKEN=localStorage.getItem("tgbtoken1");
		if (AUTH_TOKEN) {
			config.headers.token = AUTH_TOKEN;
		}
		return config;
	},
	err => {
		return Promise.reject(err);
	});
//  响应拦截
axios.interceptors.response.use(res=> {
	if (res.data.code == 20001) {
		Toast('您尚未登录或登录已过期！');
		setTimeout(function(){ 
			localStorage.removeItem("tgbtoken1");
			router.push({
				path:"/logo"
			})
		}, 1000);
	}
	if (res.data.code != 20001&&res.data.code != 10000&&res.data.code != 30002) {
		Toast(res.data.msg);
	}
	return res.data;
}, err=> {
	return Promise.reject(err);
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
